import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-inputstate-valid',
  templateUrl: './inputstate-valid.component.html',
  styleUrls: ['./inputstate-valid.component.css']
})
export class InputstateValidComponent  {

  public InvalidGender=true;
  public evenvalid=true;
  public verifyGender(gender){
    if(gender!='nogender'){
      this.InvalidGender=false;
    }
    else{this.InvalidGender=true;}
  }
  public changeVerifyeven(value){
    if(value%2==0){
      this.evenvalid=false;
    }
    else{
      this.evenvalid=true;
    }
  }

}
