import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-bookstore-app',
  templateUrl: './bookstore-app.component.html',
  styleUrls: ['./bookstore-app.component.css']
})
export class BookstoreAppComponent {
public bookname;
public authorname;
public isbn;  
public bookid;
public library=[];
 
public addbook(){
  let bookid=this.library.length + 1;
  
  this.library.push({BookId:bookid,BookName:this.bookname,Isbn:this.isbn,AuthorName:this.authorname});
  alert("record is inserted");
  this.bookname="";
  this.authorname="";
  this.isbn="";

}
public deleteBook(index){
  let confirm1=confirm("are you sure want to remove product");
  if(confirm1==true){
    switch(index){
      case index===1:
        this.library.splice(index,1);
        break;
    }
}
 
  }
}

