import { Component, OnInit ,Input,Output,EventEmitter } from '@angular/core';


@Component({
  selector: 'app-pracchild',
  templateUrl: './pracchild.component.html',
  styleUrls: ['./pracchild.component.css']
})
export class PracchildComponent  {
   @Input() public All=0;
   @Input() public Electronics=0;
   @Input() public Shoes=0;
   public selectedCategory='All';
  @Output()  public componentChanged:EventEmitter<string>=new EventEmitter<string>(); 
  public oncategoryChanged(){
    alert(this.selectedCategory);
       this.componentChanged.emit(this.selectedCategory)
   }  

}
