import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-pracproduct',
  templateUrl: './pracproduct.component.html',
  styleUrls: ['./pracproduct.component.css']
})
export class PracproductComponent  {

  public categories=['Electronics','Shoes'];
  public Electronics=['SamsungTv','SonyTv'];
  public Shoes=['leecooper','Reebook'];
  public selectedCategory;
  public Product;
 public productlist;
  public data=[
    {name:"samsungtv",price:4000,category:"Electronics"},
    {name:"SonyTv",price:4000,category:"Electronics"},
    {name:"leecooper",price:4000,category:"Shoes"},
    {name:"samsungtv",price:4000,category:"Shoes"}
]
  
  public onCategoryChanged(){
    switch(this.selectedCategory){
      case 'Electronics':
         this.Product=this.Electronics;
         break;
      case 'Shoes':
        this.Product=this.Shoes;
        break;   
    }
  }

}
