import { Component, OnInit,Input,Output,EventEmitter } from '@angular/core';

@Component({
  selector: 'app-medical-exam-result',
  templateUrl: './medical-exam-result.component.html',
  styleUrls: ['./medical-exam-result.component.css']
})
export class MedicalExamResultComponent  {
 @Input() public All=0;
 @Input() public Medical=0;
 @Input() public Enginnering=0;
 
 public selectedCategoryValue="All"
 @Output() public componentchanged:
 EventEmitter <string>=new EventEmitter<string>();
 public oncategorychanged(){
   this.componentchanged.emit(this.selectedCategoryValue);
 }
 

}
