import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-changedemo',
  templateUrl: './changedemo.component.html',
  styleUrls: ['./changedemo.component.css']
})
export class ChangedemoComponent  {
public listcategory;
public listproduct;
public productname;
public productprice;
public imgpreview;
public id;
public Productid;
public i;
public totalprice=0;

public shoppingcart=[];

 public categories=['electronics','shoes'];
 public electronics=['samsungTv','sony'];
 public shoes=['nike casuals','leecooperboot'];
 public data=[{Name:'samsung tv',photo:"assets/1.jpg",price:3000},
        {Name:'Sony mobile',photo:"assets/3.jpg",price:4000},
        {Name:'LeeCooper', photo:"assets/2.jpg",price:6000},
        {Name:'Leecooperblack',photo:"assets/4.jpg",price:7000}
]
 public products;
 public categorychanged(){
   switch(this.listcategory){
     case 'electronics':
       this.products=this.electronics;
       break;
     case 'shoes':
       this.products=this.shoes;
       break;  
   }
 }
 public showdetails(index){
    this.productname=this.data[index].Name;
    this.productprice=this.data[index].price;
    this.imgpreview=this.data[index].photo;
 }
 public datachanged(){
   switch(this.listproduct){
     case 'samsungTv':
       this.showdetails(0);
       break;
     case 'sony':
         this.showdetails(1);
         break;
     case 'nike casuals':
           this.showdetails(2);
           break;
     case 'leecooperboot':
       this.showdetails(3);
       break;


   }
 }
 public addCart(){
        this. id=this.shoppingcart.length+1;
        this.shoppingcart.push({ProductId:this.id,Name:this.productname,Price:this.productprice,Preview:this.imgpreview})
        
      }  
  
  public delCart(i){
    this.shoppingcart.splice(i,1)
  
    
  }

}
